/*============================================================================
	#Banner Widgets
==============================================================================*/
.widget-banner {
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  width: 100%;
  height: 544px; }
  .widget-banner:hover::before {
    opacity: 1; }
  .widget-banner:hover .button-primary {
    opacity: 1;
    visibility: visible;
    transform: translateY(0); }
  .widget-banner::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.94) 70.31%, #000000 100%);
    z-index: 1;
    opacity: 0;
    transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1); }
  .widget-banner__inner {
    padding: 24px 24px 80px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    background-color: #070326; }
  .widget-banner__title {
    position: relative;
    z-index: 2;
    align-self: flex-start;
    margin: 0 0 0 0; }
  .widget-banner img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    object-fit: cover; }
  .widget-banner .button-primary {
    position: absolute;
    visibility: hidden;
    transform: translateY(20px);
    opacity: 0;
    z-index: 2;
    transition: all 0.8s cubic-bezier(0.165, 0.84, 0.44, 1); }

