/*============================================================================
	#Menu's
==============================================================================*/

.header-nav {


}

.main-menu {

	list-style: none;
	margin: 0;
	padding: 0;
	display: flex;
	border-left: 1px solid #2C3057; 
	border-right: 1px solid #2C3057; 
	position: relative;

	a {

		color: white;
		text-decoration: none;
		transition: all 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);
	}

	> li > a {
		
		position: relative;
		font-size: 16px;
		font-weight: $medium;
		display: flex;
		align-items: center;
		padding: 9px 48px 9px;
		border-left: 1px solid #2C3057; 
		border-right: 1px solid #2C3057;

		&::before,
		&::after {

			content: '';
			background: #2C3057;
			position: absolute;
			top: 0;
			left: 0;
			width: 0%;
			height: 100%;
			z-index: -1;
			transition: all 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);
		}

		&::after {

			left: auto;
			right: 0;
		}
	}

	> li:hover > a {

		&::before,
		&::after {

			width: 50%;
			opacity: 1;
		}
	}

	> li > a svg {

		fill: $colorSky;
		margin-right: 16px;
		width: 20px;
		height: 20px;
	}
}

.main-menu > li:hover > .megamenu.sub-menu {

	opacity: 1;
	visibility: visible;
	pointer-events: all;
	transform: translateX(0);
}

.main-menu .sub-menu {

	margin-top: 16px;

	position: absolute;
	z-index: 1;
	border-radius: 10px;
	border: 1px solid rgba(255, 255, 255, 0.4);
	// overflow: hidden;

	&::after {

		content: '';
		position: absolute;
		top: -18px;
		left: 0;
		background: transparent;
		width: 100%;
		height: 100%;
		z-index: -1;
	}

	&::before {

		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		// box-shadow: inset 0 0 2000px rgba(22, 16, 57, 0.5);
    	// filter: blur(10px);
		background: rgba(22, 16, 57, 0.9);
		z-index: -1;
		border-radius: 10px;
		overflow: hidden;
	}
}

//    backdrop-filter 
@supports (backdrop-filter: blur()) {

	.main-menu .sub-menu {

		background: rgba(22, 16, 57, 0.5);
		backdrop-filter: blur(20px);

		&::before {
			display: none;
		}
	}
}

.sub-menu {

	opacity: 0;
	visibility: hidden;
	pointer-events: none;

	li {

		position: relative;

		&::before {
		
			content: '';
			position: absolute;
			left: -24px;
			width: 15px;
			height: 15px;
			background: url('../icons/arrow-angel-next.svg') no-repeat;
			background-position: center;
			background-size: content;
			opacity: 0;
			transform: translateX(50%);
			transition: all 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);
		} 

		&:hover {

			&::before {

				opacity: 1;
				transform: translateX(0);
			}

			a {

				color: $colorSky;
			}
		}
	}

	li.active {

		&::before {

			opacity: 1;
			transform: translateX(0);
		}

		a {

			color: $colorSky;
		}
	}

	a {

		font-size: 18px;
		font-weight: $light;
	}
}

.megamenu {

	padding: 42px 52px;
	transform: translateY(20px);
	transition: all 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);

	&__row {

		display: flex;
		margin: 0 -16px;
	}

	&__col {

		margin: 0 16px;
	}

	&__col-3 {

		flex: 0 0 280px;
		width: 280px;
	}

	&__col-title {

		margin: 0;
		font-size: 18px;
		font-weight: $medium;
		margin-bottom: 20px;
	}

	&__list {

		padding: 0;
		margin: 0;
		list-style-type: none;

		li {

			margin-bottom: 12px;
		}
	}
}